
html, button, input, select, textarea, .pure-g [class *= "pure-u"] {
    font-family: Droid Sans, Helvetica, sans-serif;
}

body {
    color: #000;
    font-size: 18px;
    max-width: 700px;
    margin: 0 auto;
    padding: 16px;
}

h1, h2, h4, h5, h6, .title {
    font-family: Droid Sans, Helvetica, sans-serif;
    font-weight: normal;
}

a, .linkcolor, .title {
    color: #ff0000;
}

.nav a {
    text-decoration: none;
    display: inline-block;
    margin: 0 1em 0 0;
}

.nav {
    padding: 1em 0px 0.5em;
    margin-bottom: 2em;
    border-bottom: 1px solid #eee;
}

.footer {
    text-align: center;
    padding-top: 55px;
    padding-bottom: 44px;
}

.left {
    float: left !important;
}
.right {
    float: right !important;
}
.clear {
    clear: both !important;
}

.navlink {
   font-family: Droid Sans, Helvetica, sans-serif;
   text-transform: capitalize;
}

.search_container {
    position: relative;
}

#productsearch {
    width: 180px;
    font-size: 0.74em;
    padding: 0.25em 2em;
}